export class GithubHandler{

  private provider;
  private credential;

  constructor () {
    this.provider = new firebase.auth.GithubAuthProvider();
    // provider.addScope('repo');
    // provider.setCustomParameters({
    //   allow_signup: 'false',
    // });
  }

  public singin () {
    firebase.auth().signInWithPopup(this.provider).then((res) => {
      this.credential = res.credential;
      this.get_user_info().then((user_info) => {
        alert(`! ${user_info.name}!`);
      });
    }).catch((err) => {
      console.log(err);
      alert('');
    });
  }

  public get_user_info() {
    return fetch('https://api.github.com/user', {
      method: 'GET',
      headers: {
        Authorization: `token ${this.credential.accessToken}` ,
        'Content-Type': 'application/json; charset=utf-8',
      },
    }).then((res) => {
      return res.json();
    }).then((json) => {
      console.log(json);
      return json;
    }).catch((err) => {
      console.log(err);
    });
  }
}
